.wrapper {
	grid-area: c;
	background-color: rgb(25, 91, 69);
	border-radius: 0 0 25px 0;
}

.userPhoto {
	height: 95px;
	width: 95px;
	border-radius: 50%;
	margin: 10px 10px;
}

.buttoncenter {
	display: flex;
	justify-content: center;
}

button {
	color: rgb(51, 80, 22);
	font-weight: bold;
	letter-spacing: 1.5px;
	background-color: rgb(167, 152, 152);
	border-radius: 30px;
}

button:hover {
	background-color: rgb(75, 209, 164);
}

